.login {
  max-width: 350px;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.login form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.username,
.password {
  width: 100%;
  margin: 10px 0; 
  padding: 10px; 
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.login-btn {
  background-color: #0095f6;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px; 
}

.login-btn:disabled {
  background-color: #b2dffc;
  cursor: not-allowed;
}

.login-btn:hover {
  background-color: #0083e8;
}

p {
  margin-top: 10px;
  font-size: 14px;
}

p span {
  color: #0095f6;
  cursor: pointer;
}

p span:hover {
  text-decoration: underline;
}



/* CSS for Navbar component */

.home-container {
  background-color: #0095f6;
  color: #fff;
  padding: 10px 0;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.head {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.home-heading-style {
  font-size: 20px;
  font-weight: bold;
}

.button-css {
  background-color: #fff;
  color: #0095f6;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button-css:hover {
  background-color: #007acc;
  color: #fff;
}

/* CSS for Admin component */



.blogpost_tittle,
.blogpost_description {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.blogpost_description {
  height: 100px;
    
}

.blogpost_button {
  background-color: #0095f6;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;
  display: inline-block;
  transition: background-color 0.3s ease;
}

.blogpost_button:hover {
  background-color: #0083e8;
}
 

 /* App.css */

#blogpost_heading {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333; 
}

form {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;  
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);  
}

.blogpost_name {
  display: block;
  margin-bottom: 20px;
  font-size: 16px;
  color: #555; 
}

.blogpost_tittle,
.blogpost_description {
  width: 100%;
  padding: 10px;
  border: 1px solid #e0e0e0;  
  border-radius: 4px;
  font-size: 16px;
  color: #333; 
}

.blogpost_tittle:focus,
.blogpost_description:focus {
  outline: none;
  border-color: #2684fe;  
}

.blogpost_button {
  background-color: #3897f0;  
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.blogpost_button:hover {
  background-color: #2684fe;  
}


 
.oauth-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.signup-button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.signup-button.google-button {
  background-color: #4285f4;  
}

.signup-button.github-button {
  background-color: #333;  
}

.signup-button:hover {
  background-color: #555;  
}


 
 .login-buttons-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

 .login-button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  width: 200px; 
}

 .login-button.google-button {
  background-color: #4285f4; 
}

 .login-button.github-button {
  background-color: #333; 
}


.login-button:hover {
  background-color: #555;  
}
